<template>
  <div>
    <formulate
      class="my-form"
      @submit="saveForm"
      name="createPerson"
    >
      <formulate-element
        name="codigo"
        type="hidden"
        placeholder="-1"
      />
      
        <article class="tile is-child box">
          <h3 class="title">Person</h3>

          <div class="columns is-mobile is-multiline is-centered">

            <div class="column is-narrow">
              <div class="control">
                <label class="label">Nombre</label>

                <formulate-element
                  name="nombre"
                  type="text"
                  placeholder="Nombre y Appellidos"
                />

              </div>
            </div>

            <div class="column">
              <div class="control">
                <label class="label">Tipo de Identificacion</label>

                <formulate-element
                  class="select is-small"
                  name="tipo"
                  type="select"
                  initial="_05"
                  :options="typesId"
                />
 

              </div>
            </div>

            <div class="column">
              <div class="control">
                <label class="label">Identifiacion</label>

                <formulate-element
                  name="ruc_cedula"
                  type="text"
                  placeholder="Codigo de identificacion"
                />

              </div>
            </div>

            <div class="column">
              <div class="control">

                <label class="label">Distribuidor?</label>
                <formulate-element
                  type="checkbox"
                  label="  "
                  name="distribuidor"
                />

              </div>
            </div>

            <div class="column">
              <div class="control">

                <label class="label">Retencion?</label>
                <formulate-element
                  type="checkbox"
                  label="  "
                  name="retencion"
                />

              </div>
            </div>

            <div class="column">
              <div class="control">
                <label class="label">Telefono</label>

                <formulate-element
                  name="telefono"
                  type="tel"
                  placeholder="# de telefono"
                />

              </div>
            </div>

            <div class="column">
              <div class="control">
                <label class="label">Direccin</label>

                <formulate-element
                  name="direccion"
                  type="text"
                  placeholder="Direccin"
                />

              </div>
            </div>

            <div class="control">

              <formulate-element
                type="submit"
                name="Save"
                elementClasses="button is-info"
              />
            </div>

          </div>

        </article>

    </formulate>
    
  </div>
</template>

<script>

import { mapGetters, mapActions } from 'vuex'; // eslint-disable-line no-unused-vars

const LG = console.log; // eslint-disable-line no-console, no-unused-vars

export default {
  props: ['pers'],
  data() {
    return { values: false };
  },
  computed: {
    ...mapGetters('person', {
      enums: 'getEnums',
    }),
    typesId() {
      LG('CC %%%%%%%%%%%%%%%%%%');
      // LG(this);
      const ret = [];
      const types = this.enums.DocTypeLookup;
      if (types) {
        Object.keys(types).forEach((value) => {
          const name = types[value];
          ret.push({
            name,
            value,
            id: value,
            label: name,
          });
        });
      } else {
        ret.push({
          name: 'aa',
          value: 'AA',
          id: 'AA-',
          label: 'aa-',
        });
      }
      LG(ret);
      return ret;
    },
  },
  methods: {
    qtst() {
      LG(' ------- Quick Test -------');
      this.onCreatePerson();
    },
    onCreatePerson() {
      this.createPerson({
        data: {
          store: 'person',
          mode: 'post',
          data: {
            ruc_cedula: '0708217086001',
            nombre: 'Jesu Cristo',
            direccion: '#1 Pearly Gates',
            telefono: '099-444-4719',
            distribuidor: 'si',
            retencion: 'si',
            tipo: '_04',
            scabetti: '333',
            tipo_de_documento: 'RUC',
          },
        },
      });
    },

    ...mapActions('person', {
      saveForm: 'saveForm',
    }),
  },
};

</script>

<style>
</style>



